.mask {
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(224, 227, 239, .7);
  justify-content: center;
  align-items: center;
  transition: all .6s;
  display: flex;
  position: fixed;
  top: 0;
}

.middle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bar {
  width: 10px;
  height: 70px;
  transform-origin: bottom;
  background: #fff;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  animation: loader 1.2s linear infinite;
  display: inline-block;
}

.bar1 {
  animation-delay: .1s;
}

.bar2 {
  animation-delay: .2s;
}

.bar3 {
  animation-delay: .3s;
}

.bar4 {
  animation-delay: .4s;
}

.bar5 {
  animation-delay: .5s;
}

.bar6 {
  animation-delay: .6s;
}

.bar7 {
  animation-delay: .7s;
}

.bar8 {
  animation-delay: .8s;
}

@keyframes loader {
  0% {
    background: tomato;
    transform: scaleY(.1);
  }

  50% {
    background: #9acd32;
    transform: scaleY(1);
  }

  100% {
    background: none;
    transform: scaleY(.1);
  }
}

.hide {
